@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.projects {
  &-page {
    padding-bottom: 128px;

    @include mixin.wide-tablet {
      padding-bottom: 64px;
    }

    .title-1 {
      @include mixin.wide-tablet {
        margin-bottom: 16px;
      }
    }

    .subtitle-1 {
      margin-bottom: 64px;

      @include mixin.wide-tablet {
        margin-bottom: 32px;
      }
    }
  }

  &__sub-text {
    margin-bottom: 54px;
    background: var.$tertiary;
    box-sizing: border-box;
    padding: 59px 101px 47px 96px;
    font-size: 24px;
    line-height: 1.66;
    font-weight: 500;
    color: var.$secondary;

    @include mixin.tablet {
      margin-bottom: 34px;
      padding: 29px 41px 27px 46px;
      font-size: 1.8rem;
    }

    .quote {
      font-size: 3rem;
    }
  }
}

.project-list {
  margin-bottom: 96px;
  position: relative;

  @include mixin.small-tablet {
    margin-bottom: 64px;
  }

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 48px;
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  .swiper-slide {
    width: fit-content;
  }

  .project-list-slider {
    width: 100%;
    overflow: unset;

    &__body {
      position: relative;
    }

    &__control {
      display: flex;
      align-items: flex-end;
      gap: 12px;

      @include mixin.phone {
        display: none;
      }
    }

    &__btn {
      display: flex;
      padding: 8px 16px;
      justify-content: center;
      align-items: center;
      border: 1px solid rgba(var.$secondary, 0.1);

      &--left {
        border-top-left-radius: 16px;
        border-bottom-right-radius: 16px;
      }

      &--right {
        border-bottom-left-radius: 16px;
        border-top-right-radius: 16px;
        transform-origin: center;
        transform: rotateZ(180deg);
      }
    }
  }

  .project-item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 97px;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.66;
    letter-spacing: 0.35px;
    color: var.$secondary;

    @include mixin.phone {
      padding-left: 36px;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 125px;
      width: 100%;
      height: 12px;
      background: var.$tertiary;

      @include mixin.phone {
        top: 100px;
      }
    }

    &__icon {
      width: 96px;
      height: 96px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var.$tertiary;
      transform: translateX(-50%);

      @include mixin.phone {
        width: 72px;
        height: 72px;
        transform: translateX(-30%);

        svg {
          max-width: 50%;
          max-height: 60%;
        }
      }
    }

    &__sign {
      transform: translateX(-50%);

      img {
        width: 72px;
        height: 82px;
      }

      @include mixin.phone {
        transform: translateX(-30%);
      }
    }

    &__text {
      span {
        font-weight: 700;
      }
    }
  }

  .swiper-slide {
    &:first-child {
      .project-item {
        &::before {
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;
        }
      }
    }

    &:last-child {
      .project-item {
        padding-right: 48px;

        &::after {
          border-top-right-radius: 10px;
          border-bottom-right-radius: 10px;
        }
      }
    }
  }
}

.project-tab {
  &__heads {
    display: flex;
    align-items: flex-start;
    gap: 64px;
    margin-bottom: 16px;
    padding-bottom: 24px;
    border-bottom: 1px solid var.$line2;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.66;
    overflow-y: auto;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    @include mixin.tablet {
      margin-bottom: 0;
    }
  }

  &__head {
    color: #B1B5BD;
    transition: 0.3s linear;
    cursor: pointer;

    &--active {
      color: var.$secondary
    }
  }

  &__item {
    display: none;

    &--active {
      display: block;
    }
  }
}

.project {
  padding: 48px 24px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(var.$secondary, 0.2);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 24px 48px;
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.66;
  color: var.$secondary;
  letter-spacing: 0.35px;

  @include mixin.tablet {
    flex-wrap: wrap;
    padding: 32px 0 24px;
  }

  &:last-child {
    border-bottom: unset;
  }

  &__head {
    min-width: 30%;
    width: 30%;

    @include mixin.phone {
      width: auto;
    }
  }

  &__title {
    font-size: 2.4rem;
    font-weight: 700;
  }

  &__list {
    flex-grow: 2;
    list-style: disc;
    padding-left: 25px;
  }

  &__img {
    position: relative;
    display: block;
    min-width: 200px;
  }
}

.support {
  padding-top: 32px;
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.66;
  letter-spacing: 0.35px;
  color: var.$secondary;

  &__title {
    font-size: 2.4rem;
    font-weight: 700;
    color: var.$secondary;
    margin-bottom: 16px;

    @include mixin.tablet {
      line-height: 1.33;
    }
  }

  &__subtitle {
    color: var.$gray;
    margin-bottom: 48px;

    @include mixin.tablet {
      margin-bottom: 32px;
    }
  }

  &__body {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 32px 48px;

    @include mixin.tablet {
      flex-wrap: wrap;
    }

    ul {
      list-style: disc;
      padding-left: 28px;

      li {
        margin-bottom: 16px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    img {
      min-width: 45.7%;
      max-width: 100%;

      @include mixin.tablet {
        min-width: unset;
      }
    }
  }
}

.analitic-text {
  padding-top: 32px;
  font-size: 2.4rem;
  font-weight: 300;
  line-height: 1.66;
  letter-spacing: 0.83px;
  color: var.$secondary;
  margin-bottom: 32px;

  @include mixin.tablet {
    font-size: 1.8rem;
    line-height: 1.55;
    letter-spacing: 0.3px;
    margin-bottom: 16px;
  }
}

.analitic-btn {
  padding: 12px 16px;
  margin-bottom: 64px;
  text-align: center;
  display: inline-block;
  align-self: flex-start;
  min-width: 414px;
  max-width: 100%;
  box-sizing: border-box;

  @include mixin.tablet {
    width: 100%;
    padding: 8px 16px;
    margin-bottom: 32px;
    min-width: unset;
  }
}

.analitic {
  gap: 64px 0;

  @include mixin.tablet {
    gap: 32px 0;
  }

  &__head {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 24px;
  }

  &__icon {
    flex-shrink: 0;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background: var.$tertiary;
    display: flex;
    justify-content: center;
    align-items: center;

    @include mixin.tablet {
      width: 72px;
      height: 72px;

      svg {
        width: 70%;
      }
    }
  }

  &__title {
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.33;
    color: var.$secondary;

    @include mixin.tablet {
      font-size: 1.8rem;
      color: #152340;
      margin-bottom: 8px;
    }
  }

  &__text {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.66;
    color: var.$secondary;

    ul {
      list-style: disc;
      padding-left: 18px;

      li {
        margin-bottom: 16px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

.bvi-active {
  .project-item {
    &::before {
      display: none;
    }
  }
}